/* eslint-disable no-console */

import {
  type ProjectResolverWorkerProps,
  type ProjectResolverWorkerResponse,
  type WorkerData,
} from '@src/workers/types'

function handleEvent(
  e: WorkerData<ProjectResolverWorkerProps>
): ProjectResolverWorkerResponse {
  const { selectedWorkspaceId, projects, projectName } = e.data

  const matches = Object.values(projects ?? {}).filter(
    (item) => item.name === projectName
  )

  const found = matches.find(
    (item) => item.workspace_id === selectedWorkspaceId
  )

  return found ?? matches[0]
}

self.onmessage = function (e: WorkerData<ProjectResolverWorkerProps>) {
  try {
    console.time('toggl:projectResolverWorker')
    const response = handleEvent(e)
    self.postMessage(response)
    console.timeEnd('toggl:projectResolverWorker')
  } catch (error) {
    console.error(error, 'toggl:projectResolverWorker')
  }
}
